@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;700;800;900&display=swap);
.rec-carousel-item:focus {
  outline: none;
  box-shadow: inset 0 0 1px 1px lightgrey;
}

.rec.rec-arrow {
  border-radius: 0;
  background-color: #ed9121;
  color: #eeeeee;
}

.rec.rec-arrow:hover:enabled , .rec.rec-arrow:focus:enabled {
  border-radius: 0;
  background-color: #222831;
  color: #eeeeee;
}

.rec.rec-arrow:disabled {
  visibility: hidden;
}

button.rec-dot{
  background-color: #eeeeee;
  box-shadow: 0 0 1px 3px #222831;
}


button.rec-dot:hover, button.rec-dot:active, button.rec-dot:focus  {
  background-color:  #ff0021;
  box-shadow: 0 0 1px 3px rgba(235,16,16,0.5);
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat' , sans-serif;

}

.rec-carousel-item:focus {
  outline: none;
  box-shadow: inset 0 0 1px 0px red;
}
